import React, { Component } from 'react'
import UserInfo from './userinfo'
import UserOthers from './userothers'
import Feedback from './feedback'
import Hola from './hola'

export default class Aform extends Component {
  constructor(props) {
    super(props)
    this.state = {
      firstName: { value: '', valid: false, message: 'fill in man!' },
      lastName: { value: '', valid: false, message: 'fill in man!' },
      email: { value: '', valid: false, message: 'fill in man!' },
      age: { value: '', valid: false, message: 'fill in man!' },
      skills: { value: '', valid: false, message: 'fill in man!' },
      page: 0,
    }
  }

  validate = (tname, tvalue) => {
    let infos = { ...this.state }
    console.log(tname, tvalue)
    infos[tname].value = tvalue
    switch (tname) {
      case 'firstName':
        if (infos.firstName.value.length < 3) {
          infos.firstName.message = 'should be >=3 characters.'
          infos.firstName.valid = false
        } else if (infos.firstName.value && infos.firstName.value.length > 15) {
          infos.firstName.message = 'should be <=15 characters.'
          infos.firstName.valid = false
        } else {
          infos.firstName.valid = true
          infos.firstName.message = infos.firstName.value
        }
        break
      case 'lastName':
        if (infos.lastName.value.length < 3) {
          infos.lastName.message = 'should be >=3 characters.'
          infos.lastName.valid = false
        } else if (infos.lastName.value && infos.lastName.value.length > 15) {
          infos.lastName.message = 'should be <=15 characters.'
          infos.lastName.valid = false
        } else {
          infos.lastName.valid = true
          infos.lastName.message = infos.lastName.value
        }

        break

      case 'email':
        if (infos.email.value.length < 3) {
          infos.email.message = 'should be >=3 characters.'
          infos.email.valid = false
        } else if (infos.email.value && infos.email.value.length > 15) {
          infos.email.message = ' should be <=15 characters.'
          infos.email.valid = false
        } else {
          infos.email.valid = true
          infos.email.message = infos.email.value
        }

        break
      case 'age':
        const regx = !/^[0-9]+$/
        const v = infos.age.value
        if (v.match(regx)) {
          infos.age.message = ' age should be number.'
          infos.age.valid = false
        } else {
          infos.age.valid = true
          infos.age.message = infos.age.value
        }

        break

      case 'skills':
        if (infos.skills.value.length < 3) {
          infos.skills.message = ' should be >=3 characters.'
          infos.skills.valid = false
        } else if (infos.skills.value && infos.skills.value.length > 15) {
          infos.skills.message = 'should be <=15 characters.'
          infos.skills.valid = false
        } else {
          infos.skills.valid = true
          infos.skills.message = infos.skills.value
        }
        break
    }

    this.setState({ ...infos })
  }

  handleOnChange = e => {
    e.preventDefault()
    const { name, value } = e.target
    this.validate(name, value)
    console.log(name)
    // this.setState({ [name]: { ...this.state[name], value: value } })
    // console.log(name)
  }

  nextPage = () => {
    const { page, max_pages } = this.state
    this.setState({ page: page + 1 })
  }

  prevPage = () => {
    const { page, max_pages } = this.state
    this.setState({ page: page - 1 })
  }

  render() {
    const { page } = this.state
    const infos = { ...this.state }
    console.log(page)
    switch (page) {
      case 0:
        return (
          <div className="box margin-top">
            <h1>page : {page}</h1>
            <UserInfo
              handleOnChange={this.handleOnChange}
              nextPage={this.nextPage}
              infos={infos}
            />
          </div>
        )

      case 1:
        return (
          <div className="box margin-top">
            <h1>page : {page}</h1>
            <UserOthers
              handleOnChange={this.handleOnChange}
              nextPage={this.nextPage}
              prevPage={this.prevPage}
              infos={infos}
            />
          </div>
        )
      case 2:
        return (
          <div className="box margin-top">
            <h1>page : {page}</h1>
            <Feedback
              prevPage={this.prevPage}
              nextPage={this.nextPage}
              infos={infos}
            />
          </div>
        )

      case 3:
        return (
          <div className="box margin-top">
            <h1>page : {page}</h1>
            <Hola />
          </div>
        )

      default:
        break
    }
  }
}
