import React, { Component } from 'react'
import '../../assets/css/ml.css'
import classNames from 'classnames'
export default class Feedback extends Component {
  render() {
    const { infos } = this.props
    const disabled =
      infos.firstName.valid &&
      infos.lastName.valid &&
      infos.age.valid &&
      infos.email.valid &&
      infos.skills.valid

    console.log(disabled, 'disabe')

    return (
      <div className=" cspan border-1 ">
        <h1> Feedback </h1>

        <div className="grid-2 border-1">
          <h1>FirstName</h1>
          <span
            className={classNames({
              'nspan-r': !infos.firstName.valid,
              'nspan-g': infos.firstName.valid,
            })}
          >
            {infos.firstName.message}
          </span>

          <h1>LastName</h1>
          <span
            className={classNames({
              'nspan-r': !infos.lastName.valid,
              'nspan-g': infos.lastName.valid,
            })}
          >
            {infos.lastName.message}{' '}
          </span>

          <h1>Email</h1>
          <span
            className={classNames({
              'nspan-r': !infos.email.valid,
              'nspan-g': infos.email.valid,
            })}
          >
            {infos.email.message}
          </span>

          <h1>Age</h1>
          <span
            className={classNames({
              'nspan-r': !infos.age.valid,
              'nspan-g': infos.age.valid,
            })}
          >
            {infos.age.message}
          </span>

          <h1>Skills</h1>
          <span
            className={classNames({
              'nspan-r': !infos.skills.valid,
              'nspan-g': infos.skills.valid,
            })}
          >
            {infos.skills.message}
          </span>
        </div>

        <div className="btdiv">
          <button
            className=" special boxshadow"
            onClick={() => this.props.prevPage()}
          >
            Edit
          </button>

          <button
            className=" special boxshadow"
            disabled={!disabled}
            onClick={() => this.props.nextPage()}
          >
            Confirm!
          </button>
        </div>
      </div>
    )
  }
}
